.section-products{
    padding-top:121px;
    padding-bottom:98px;



    & .container {

        text-align: center;
        }


      &__title{
            font-family:$title-font;
            font-weight: 400;
            font-size: 18px;
            line-height: 1.167;
            text-align: center;
            text-transform: uppercase;
            color:$secondary-title-color;
            margin-bottom:15px;
            }


            &__heading {
                font-family: $title-font;
                font-size: 34px;
                line-height: 1.147;
                text-align: center;
                letter-spacing: 0.04em;
                text-transform: uppercase;
                color: $main-title-color;
                margin-bottom: 31px;
                }

}


.products{
    display:flex;
    align-items:center;
    flex-direction:column;
    justify-content:center;
    width:100%;
    list-style:none;



   &__item{
        position:relative;
        border-radius:24px;
        align-items: center;
        justify-content: center;
        margin-left: auto;
        margin-right: auto;
        width:100%;
    }

   &__item:first-child{
        margin-top:130px;
    }
    
    &__item:not(:last-child){
        margin-bottom:160px;
    }
    
    &--icecream{
        background-color: $hero-background-color;
    }
    
   &--coffee{
        background-color: $product2-color;
    }
    
    &--milkshake{
        background-color: $product3-color;
    }


    &__photo {
        position: absolute;
        width:249px;
        height:252px;
        display:flex;
        align-items: center;
        justify-content: center;
         top:-150px;
        right:50%;
    transform:translateX(50%);}




    &__title{
        padding-top:120px;
        font-family: $title-font;
        font-weight: 400;
        font-size: 20px;
        line-height: 1.15;
        text-align: center;
        letter-spacing: 0.04em;
        color:$main-background-color;}
        
        &__container{
        
            margin-right:auto;
            margin-left:auto;
        }
        
        &__dot{
            display: inline-block;
                width: 6px;
                height: 6px;
                border-radius: 50%;
                background: $main-background-color;
        }
        
        &__dot:not(:last-child){
            margin-right:4px;
        }
        &__text{
            width:200px;
            font-family: $main-font;
            font-weight: 700;
            font-size: 14px;
             line-height: 152%;
            text-align: center;
             letter-spacing: 0.04em;
             color: $main-background-color;
             margin-right:auto;
             margin-left:auto;
             margin-bottom:30px;
        
        }
        
        &__link {
            display: inline-block;
            width: 40px;
            height: 40px;
          }

        &__arrow {
            width: 100%;
            border-radius: 50%;
           margin-bottom:41px;
          }


}









  @media screen and (min-width:768px){

    .section-products{
      
        padding-bottom:112px;

        & .container {

            text-align: center;
            padding-left: 0;
            padding-right: 0;
            }
    
    
    
          &__title{
              
                font-size: 26px;
                line-height: 1.153;
                }
    
    
                &__heading {
                   
                    font-size: 48px;
                    margin-bottom: 39px;
                    }
    
    }
 
    
    .products{
        padding-left:0px;
        display:flex;
        margin-top:200px;
    margin-left: 30px;
    margin-right:30px;
        flex-direction:row;
       flex-wrap: wrap;
      width:750px;
        list-style:none;
        margin-bottom:0px;
        justify-content: center;
        align-items: center;
    
    
    
       &__item{
            position:relative;
            border-radius:24px;
           width: 220px;
            flex-basis: calc(100%/3-20px);
       
            
        }
    
        &__item:first-child{
            margin-top:0;
            margin-bottom:0;
        }
        
        &__item:not(:last-child){
            margin-bottom:0;
        }
    
    

    
    }

  }


  @media screen and (min-width:1200px){
   
    

    .products{
   margin-left: auto;
   margin-right: auto;
      width:1100px;
     


       &__item{
           
            border-radius:24px;
            width: 335px;
           
       
            
        }
    

    &__title{
        font-size: 30px;
        line-height: 1.133;  

    }
}
  }